@import 'fonts';
@import 'mixin';
@import 'variables';

// reset
*,
::after,
::before {
    box-sizing: border-box;
}

* {
    margin: 0;
    padding: 0;
}

body {
    font-family: $body-font;
    color: black;
    font-size: 15px;
    letter-spacing: -0.24px;
}


a {
    color: inherit;
    text-decoration: none;
    background-color: transparent;

    &:hover {
        text-decoration: none;
        color: black;
    }
}


img {
    max-width: 100%;
}

//end of reset

//ultilities
.u-width-100 {
    width: 100%;
}

.u-flex-grow-0 {
    flex-grow: 0;
}

.u-fz-13 {
    font-size: 13px;
}


.u-fz-14 {
    font-size: 14px;
}

.u-fz-20 {
    font-size: 20px;
}

.u-fz-60 {
    font-size: 60px;
}

.u-color-blue {
    color: #003482;
}

.u-color-red {
    color: #FF4912;
}

.u-min-width-275 {
    min-width: 275px;
}

.u-max-width-275 {
    max-width: 275px;
}

.u-text-align-right {
    text-align: right;
}


.u-bg-white {
    background: white;
}

//end of ultilities
//style chung

.tieu-de-page {

    font-family: $heading-font;
    font-style: normal;
    font-weight: 600;
    font-size: 30px;
    line-height: 1;
    display: flex;
    align-items: center;
    letter-spacing: -0.24px;
    color: #000000;
}

.button-click-page {
    background: linear-gradient(180deg, #A7A9AC 0%, #D1D3D4 100%);
    border-radius: 10px;
    padding: 12px 16px;
    color: $white;
    font-family: $heading-font;
    font-style: normal;
    font-weight: bold;
    font-size: 15px;
    line-height: 1.33;
    letter-spacing: -0.24px;
    text-transform: uppercase;

    &:hover {
        background: linear-gradient(180deg, #FF4912 0%, #FF7900 100%);
        color: $white;
    }
}

select {
    -webkit-appearance: none;
    -moz-appearance: none;
    background-image: url(../images/icon-triangle-down.png);
    background-position: calc(100% - 10px) 50%;
    background-repeat: no-repeat;
    background-size: auto auto;
}

.select-dv {
    background: none;
    outline: none;
    border: 0.5px solid $border-color;
    box-sizing: border-box;
    border-radius: 10px;
    font-family: $body-font;
    font-style: normal;
    font-weight: normal;
    font-size: 15px;
    line-height: 1.33;
    letter-spacing: -0.24px;
    //text-transform: uppercase;
    color: $black;
    padding: 12px 16px 12px 10px;
    -webkit-appearance: none;
    -moz-appearance: none;
    background-image: url(../images/icon-triangle-down.png);
    background-position: calc(100% - 10px) 50%;
    background-repeat: no-repeat;
    background-size: auto auto;
}

.input-page {
    background: none;
    outline: none;
    border: 0.5px solid $border-color;
    box-sizing: border-box;
    border-radius: 10px;
    font-family: $body-font;
    font-style: normal;
    font-weight: normal;
    font-size: 15px;
    line-height: 1.33;
    letter-spacing: -0.24px;
    //text-transform: uppercase;
    color: $black;
    padding: 12px 16px;

    // style placeholder color and hide when click
    /*WebKit browsers*/
    &::-webkit-input-placeholder {
        color: $gray-color;
        opacity: 1;
    }

    &:focus::-webkit-input-placeholder {
        color: transparent;
    }

    /* Mozilla Firefox 4 to 18 */
    &:-moz-placeholder {
        color: $gray-color;
        opacity: 1;
    }

    &:focus:-moz-placeholder {
        color: transparent;
    }

    /* Mozilla Firefox 19+ */
    &::-moz-placeholder {
        color: $gray-color;
        opacity: 1;
    }

    &:focus::-moz-placeholder {
        color: transparent;
    }

    /* Internet Explorer 10+ */
    &:-ms-input-placeholder {
        color: $gray-color;
        opacity: 1;
    }

    &:focus:-ms-input-placeholder {
        color: transparent;
    }
}


// custom class

.bg {
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    overflow: hidden;

    // pointer-events: none;
    &>img {
        min-height: 100%;
    }
}

.content-wrapper {
    position: relative;
    z-index: 10;

    &-inner {
        overflow-x: scroll;
    }

    .content {
        position: relative;
        z-index: 10;
        padding-top: 95px;

        display: flex;
        width: 1280px;
        margin-left: auto;
        margin-right: auto;


        .left-menu {
            position: relative;
            width: 25%;
            padding-top: 55px;
            padding-bottom: 25px;

            .bg {
                border-top-left-radius: 25px;
                border-bottom-left-radius: 25px;
            }

            .menu {
                position: relative;
                z-index: 10;
                color: white;


                .menu-item {
                    display: flex;
                    align-items: center;
                    font-weight: normal;
                    font-size: 15px;
                    position: relative;
                    padding-left: 55px;
                    padding-right: 15px;
                    min-height: 50px;

                    &::after,
                    &::before {
                        content: '';
                        display: block;
                        height: 1px;
                        width: 100%;
                        position: absolute;
                        bottom: 0;
                        left: 0;
                        background: linear-gradient(270deg, rgba(255, 255, 255, 0) 0%, #FFFFFF 46.76%, rgba(255, 255, 255, 0) 94.51%);
                        opacity: 0.5;
                    }

                    &::before {
                        bottom: unset;
                        top: 0;
                    }

                    &.active,
                    &:hover {
                        background: rgba(190, 210, 238, 0.4);
                        font-weight: bold;
                        color: white;
                    }

                    .icon {
                        margin-right: 13px;
                    }
                }
            }
        }

        .info-head-main-right-column {
            width: 75%;
            background-color: white;
            border-top-right-radius: 25px;
            border-bottom-right-radius: 25px;
            overflow: hidden;

            .info-head {
                display: flex;
                justify-content: space-between;
                padding-left: 30px;
                padding-right: 25px;
                color: white;
                background: linear-gradient(180deg, #F04E24 0%, #FF7900 100%);
                padding-top: 18px;
                padding-bottom: 17px;
                margin-bottom: 20px;

                .title {
                    margin-right: 25px;
                }

                .user-name {
                    text-transform: uppercase;
                }

                &.u-bg-white {
                    background: white;
                }
            }

            .main-right-column {
                display: flex;

                main {
                    flex-grow: 1;
                }

                .right-column {
                    width: 33%;
                    padding-right: 25px;
                    padding-top: 0px;
                    padding-bottom: 25px;

                    .menu {
                        background: linear-gradient(90deg, #0095DD 0%, #00CAE9 100%);
                        border-radius: 15px;
                        color: white;
                        margin-bottom: 10px;
                        min-width: 296px;

                        .menu-item {
                            font-weight: normal;
                            font-size: 15px;
                            line-height: 133%;
                            position: relative;
                            display: flex;
                            align-items: center;
                            padding-left: 30px;
                            padding-right: 15px;
                            padding-top: 18px;
                            padding-bottom: 18px;

                            &::before {
                                content: '';
                                display: block;
                                height: 17px;
                                width: 12px;
                                margin-right: 10px;
                                background-position: center center;
                                background-size: 100% 100%;
                                background-image: url(../images/icons/icon-ks-list.png);
                            }

                            &::after {
                                content: '';
                                display: block;
                                height: 1px;
                                width: 100%;
                                position: absolute;
                                bottom: 0;
                                left: 0;
                                background: rgba(white, 0.5);
                            }

                            .icon {
                                margin-right: 10px;
                            }


                            &.active,
                            &:hover {
                                font-weight: bold;
                                color: #FFBE15;

                                &::before {
                                    background-image: url(../images/icons/icon-ks-list-orange.png);
                                }
                            }
                        }
                    }

                    .ho-tro {
                        &>a {
                            display: flex;
                            align-items: center;
                            background-color: #E3E3E3;
                            border-radius: 15px;
                            padding-top: 7px;
                            padding-bottom: 7px;
                            padding-left: 12px;
                            padding-right: 15px;
                            margin-bottom: 10px;
                            margin-top: 10px;

                            .group-text {
                                margin-left: 10px;
                                font-size: 18px;

                                .phone-number {
                                    font-size: 30px;
                                    font-weight: bold;
                                    margin-top: -10px;
                                }
                            }
                        }
                    }

                    .qrcode {
                        display: flex;
                        align-items: center;
                        font-size: 20px;
                        line-height: 125%;
                        font-weight: 600;
                    }
                }
            }
        }
    }

    .di-den-trang-chu {
        position: relative;
        z-index: 10;
        max-width: 1280px;
        margin-left: auto;
        margin-right: auto;
        text-align: right;
        padding-bottom: 150px;
        text-transform: uppercase;
        margin-top: 35px;

        .btn-manhtq {
            min-width: 280px;
            padding-top: 10px;
            padding-bottom: 10px;
            font-size: 15px;

            img {
                margin-left: 10px;
            }
        }

        @include ipad-potrait {
            padding-right: 15px;
            padding-left: 15px;

            .btn-manhtq {
                min-width: unset;
            }
        }
    }
}

.btn-manhtq {
    font-weight: bold;
    font-size: 20px;
    border-radius: 10px;
    background: linear-gradient(180deg, #FF4912 0%, #FF7900 100%);
    background-repeat: no-repeat;
    color: white;
    padding-top: 20px;
    padding-bottom: 20px;
    padding-left: 30px;
    padding-right: 30px;
    cursor: pointer;

    &:not(:last-child) {
        margin-right: 10px;
    }

    &.btn-short {
        display: inline-flex;
        justify-content: center;
        align-items: center;
        height: 45px;
        font-size: 15px;
        min-width: 188px;
        text-transform: uppercase;
    }

    &.btn-grey-linear {
        background: linear-gradient(180deg, #A7A9AC 0%, #D1D3D4 100%);
    }
}